import React, { useState, useEffect, useRef } from 'react';
import { 
  Paper, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow,
  TablePagination,
  Box,
  Typography,
  Alert,
  Slider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Divider,
  IconButton,
  Tooltip,
  Button
} from '@mui/material';
import SettingsIcon from '@mui/icons-material/Settings';
import CloseIcon from '@mui/icons-material/Close';

function DataTable({ data, columns }) {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [rowHeight, setRowHeight] = useState(53); //   
  const [columnWidths, setColumnWidths] = useState({}); //   
  const [dragging, setDragging] = useState(null); //   
  const [startX, setStartX] = useState(0); //   X 
  const [startWidth, setStartWidth] = useState(0); //    
  const [showSettings, setShowSettings] = useState(false); //    
  const tableRef = useRef(null);
  
  //    
  useEffect(() => {
    if (columns && columns.length > 0) {
      const initialWidths = {};
      columns.forEach((col) => {
        initialWidths[col] = 150; //   150px
      });
      setColumnWidths(initialWidths);
    }
  }, [columns]);
  
  //     
  useEffect(() => {
    console.log('DataTable  :');
    console.log(' :', data);
    console.log(' :', typeof data);
    console.log(' :', data ? data.length : 0);
    console.log(' :', columns);
  }, [data, columns]);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  
  //    
  const handleRowHeightChange = (event, newValue) => {
    setRowHeight(newValue);
  };
  
  //     
  const handleDragStart = (e, column) => {
    setDragging(column);
    setStartX(e.clientX);
    setStartWidth(columnWidths[column]);
    
    document.addEventListener('mousemove', handleDragMove);
    document.addEventListener('mouseup', handleDragEnd);
  };
  
  //     
  const handleDragMove = (e) => {
    if (dragging) {
      const diff = e.clientX - startX;
      const newWidth = Math.max(50, startWidth + diff); //  50px
      
      setColumnWidths({
        ...columnWidths,
        [dragging]: newWidth
      });
    }
  };
  
  //     
  const handleDragEnd = () => {
    setDragging(null);
    document.removeEventListener('mousemove', handleDragMove);
    document.removeEventListener('mouseup', handleDragEnd);
  };
  
  //   
  const toggleSettings = () => {
    setShowSettings(!showSettings);
  };
  
  //    
  const resetColumnWidths = () => {
    const initialWidths = {};
    columns.forEach((col) => {
      initialWidths[col] = 150; //   150px
    });
    setColumnWidths(initialWidths);
    setRowHeight(53); //   
  };
  
  //     
  const handleColumnWidthChange = (column, width) => {
    setColumnWidths({
      ...columnWidths,
      [column]: width
    });
  };

  //   
  if (!data || !Array.isArray(data) || data.length === 0) {
    return (
      <Paper sx={{ p: 3, textAlign: 'center' }}>
        <Alert severity="info" sx={{ mb: 2 }}>
           .    .
        </Alert>
        <Typography variant="body2" color="text.secondary">
               .
        </Typography>
      </Paper>
    );
  }

  //   
  if (!columns || !Array.isArray(columns) || columns.length === 0) {
    return (
      <Paper sx={{ p: 3, textAlign: 'center' }}>
        <Alert severity="warning">
          ()  .
        </Alert>
        <Typography variant="body2" color="text.secondary">
             .
        </Typography>
      </Paper>
    );
  }

  //   
  console.log('   :', data.length > 0 ? data[0] : null);
  
  //        
  const firstRow = data[0] || {};
  const missingColumns = columns.filter(col => !(col in firstRow));
  
  if (missingColumns.length > 0) {
    console.warn('   :', missingColumns);
  }

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
        <Typography variant="h6" gutterBottom>
            ({data.length})
        </Typography>
        <Tooltip title=" ">
          <IconButton onClick={toggleSettings}>
            <SettingsIcon />
          </IconButton>
        </Tooltip>
      </Box>
      
      {showSettings && (
        <Paper sx={{ p: 2, mb: 2 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
            <Typography variant="h6"> </Typography>
            <IconButton onClick={toggleSettings}>
              <CloseIcon />
            </IconButton>
          </Box>
          
          <Divider sx={{ my: 1 }} />
          
          <Box sx={{ mb: 2 }}>
            <Typography gutterBottom> : {rowHeight}px</Typography>
            <Slider
              value={rowHeight}
              onChange={handleRowHeightChange}
              min={30}
              max={100}
              step={1}
              marks={[
                { value: 30, label: '30px' },
                { value: 53, label: '' },
                { value: 100, label: '100px' },
              ]}
            />
          </Box>
          
          <Typography gutterBottom> :</Typography>
          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
            {columns.map((column) => (
              <TextField
                key={column}
                label={column}
                type="number"
                size="small"
                value={columnWidths[column] || 150}
                onChange={(e) => handleColumnWidthChange(column, Number(e.target.value))}
                InputProps={{ inputProps: { min: 50, max: 500 } }}
                sx={{ width: 150, mb: 1 }}
              />
            ))}
          </Box>
          
          <Box sx={{ mt: 2, textAlign: 'right' }}>
            <Tooltip title="   ">
              <Button variant="outlined" color="primary" onClick={resetColumnWidths} sx={{ mr: 1 }}>
                
              </Button>
            </Tooltip>
          </Box>
        </Paper>
      )}
      
      <Paper sx={{ width: '100%', overflow: 'hidden' }}>
        <TableContainer sx={{ maxHeight: 440 }} ref={tableRef}>
          <Table stickyHeader aria-label=" ">
            <TableHead>
              <TableRow>
                <TableCell 
                  sx={{ 
                    backgroundColor: (theme) => theme.palette.primary.main,
                    color: 'white',
                    width: 60,
                    minWidth: 60
                  }}
                >
                  #
                </TableCell>
                {columns.map((column, index) => (
                  <TableCell 
                    key={index}
                    sx={{ 
                      backgroundColor: (theme) => theme.palette.primary.main,
                      color: 'white',
                      width: columnWidths[column] || 150,
                      minWidth: columnWidths[column] || 150,
                      position: 'relative',
                      userSelect: 'none'
                    }}
                  >
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                      <span>{column}</span>
                      <Box 
                        sx={{ 
                          position: 'absolute',
                          right: 0,
                          top: 0,
                          height: '100%',
                          width: '5px',
                          cursor: 'col-resize',
                          '&:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.3)'
                          }
                        }}
                        onMouseDown={(e) => handleDragStart(e, column)}
                      />
                    </Box>
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {data
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row, rowIndex) => (
                  <TableRow 
                    hover 
                    key={rowIndex}
                    sx={{ height: `${rowHeight}px` }}
                  >
                    <TableCell component="th" scope="row">
                      {page * rowsPerPage + rowIndex + 1}
                    </TableCell>
                    {columns.map((column, colIndex) => (
                      <TableCell 
                        key={colIndex}
                        sx={{ 
                          width: columnWidths[column] || 150,
                          maxWidth: columnWidths[column] || 150,
                          overflow: 'hidden',
                          textOverflow: 'ellipsis',
                          whiteSpace: 'nowrap'
                        }}
                      >
                        {row[column] !== null && row[column] !== undefined 
                          ? String(row[column]) 
                          : ''}
                      </TableCell>
                    ))}
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={data.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
          labelRowsPerPage=" :"
          labelDisplayedRows={({ from, to, count }) => 
            `${from}-${to} /  ${count}`
          }
        />
      </Paper>
    </Box>
  );
}

export default DataTable; 