import React, { useState, useEffect } from 'react';
import { ThemeProvider, CssBaseline, Box, Container, AppBar, Toolbar, Typography, IconButton, Menu, MenuItem, Snackbar, Alert, CircularProgress, Backdrop, Tabs, Tab, Button } from '@mui/material';
import PaletteIcon from '@mui/icons-material/Palette';
import StorageIcon from '@mui/icons-material/Storage';
import DashboardIcon from '@mui/icons-material/Dashboard';
import LogoutIcon from '@mui/icons-material/Logout';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import { BrowserRouter as Router, Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import FileUpload from './components/FileUpload';
import DataTable from './components/DataTable';
import OperationsPanel from './components/OperationsPanel';
import TemplateManager from './components/TemplateManager';
import UniqueValuesList from './components/UniqueValuesList';
import OperationLogs from './components/OperationLogs';
import Dashboard from './components/Dashboard';
import Login from './components/Login';
import Register from './components/Register';
import AdminDashboard from './components/AdminDashboard';
import themes from './themes';
import axios from 'axios';

// API  URL 
axios.defaults.baseURL = 'http://localhost:5000';

//   Axios    
axios.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

//     (401   )
axios.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response && error.response.status === 401) {
      //     
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

//   
const ProtectedRoute = ({ children, requireAdmin }) => {
  const isAuthenticated = localStorage.getItem('token') !== null;
  const user = JSON.parse(localStorage.getItem('user') || '{}');
  const isAdmin = user?.is_admin === true;
  
  if (!isAuthenticated) {
    return <Navigate to="/login" />;
  }
  
  if (requireAdmin && !isAdmin) {
    return <Navigate to="/" />;
  }
  
  return children;
};

function AppContent() {
  //  
  const [currentTheme, setCurrentTheme] = useState('default');
  
  //  
  const [anchorEl, setAnchorEl] = useState(null);
  const [userMenuAnchorEl, setUserMenuAnchorEl] = useState(null);
  
  //  
  const [currentTab, setCurrentTab] = useState(0);
  
  //  
  const [data, setData] = useState([]);
  const [columns, setColumns] = useState([]);
  const [filename, setFilename] = useState('');
  
  //  
  const [operations, setOperations] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  
  //  
  const [logs, setLogs] = useState([]);
  const [totalAffectedRows, setTotalAffectedRows] = useState(0);
  
  //  
  const [user, setUser] = useState(JSON.parse(localStorage.getItem('user')) || null);
  
  //  
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'info'
  });
  
  const navigate = useNavigate();
  
  //    
  useEffect(() => {
    const checkAuth = async () => {
      const token = localStorage.getItem('token');
      if (token) {
        try {
          const response = await axios.get('/api/auth/me');
          setUser(response.data.user);
        } catch (error) {
          console.error('    :', error);
          //    
          handleLogout();
        }
      }
    };
    
    checkAuth();
  }, []);
  
  //   
  const handleTabChange = (event, newValue) => {
    setCurrentTab(newValue);
  };
  
  //   
  const handleThemeMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  
  const handleThemeMenuClose = () => {
    setAnchorEl(null);
  };
  
  const handleThemeChange = (theme) => {
    setCurrentTheme(theme);
    handleThemeMenuClose();
  };
  
  //   
  const handleUserMenuOpen = (event) => {
    setUserMenuAnchorEl(event.currentTarget);
  };
  
  const handleUserMenuClose = () => {
    setUserMenuAnchorEl(null);
  };
  
  //   
  const handleAdminDashboard = () => {
    setUserMenuAnchorEl(null);
    navigate('/admin');
  };
  
  //  
  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    setUser(null);
    setUserMenuAnchorEl(null);
    navigate('/login');
  };
  
  //  
  const handleLogin = (userData) => {
    setUser(userData);
  };
  
  //   
  const handleFileUpload = (response) => {
    console.log('  :', response);
    
    //    
    if (!response || !response.success) {
      console.error('   :', response);
      setSnackbar({
        open: true,
        message: '    :    ',
        severity: 'error'
      });
      return;
    }
    
    //     
    console.log('   :', response);
    
    //      
    //    data columns  
    const responseData = response.data || [];
    const responseColumns = response.columns || [];
    
    console.log(' :', responseData);
    console.log(' :', Array.isArray(responseData) ? responseData.length : 0);
    console.log(':', responseColumns);
    
    //     
    //            
    const processedData = Array.isArray(responseData) ? responseData : [];
    
    //  
    setData(processedData);
    setColumns(responseColumns);
    setFilename(response.filename || '');
    
    //     
    setOperations([]);
    setLogs([]);
    setTotalAffectedRows(0);
    
    //   
    setSnackbar({
      open: true,
      message: `"${response.filename || ''}"   . ${processedData.length}  .`,
      severity: 'success'
    });
  };
  
  //   
  const handleProcessData = async () => {
    if (!data.length || !operations.length) {
      setSnackbar({
        open: true,
        message: '    ',
        severity: 'warning'
      });
      return;
    }
    
    setIsLoading(true);
    
    try {
      console.log('  :', { 
        operations: operations,
        dataLength: data.length
      });
      
      const response = await axios.post('/api/process', {
        data,
        operations
      });
      
      if (response.data.success) {
        console.log('  :', {
          resultLength: response.data.data.length,
          logs: response.data.logs
        });
        
        setData(response.data.data);
        setLogs(response.data.logs || []);
        setTotalAffectedRows(response.data.totalAffectedRows || 0);
        
        //     
        const allSuccess = response.data.allSuccess;
        const message = allSuccess
          ? `  . ${response.data.totalAffectedRows}  .`
          : '  .    .';
        
        setSnackbar({
          open: true,
          message: message,
          severity: allSuccess ? 'success' : 'warning'
        });
      }
    } catch (error) {
      console.error('    :', error);
      
      //       
      if (error.response?.data?.logs) {
        setLogs(error.response.data.logs);
      }
      
      setSnackbar({
        open: true,
        message: `    : ${error.response?.data?.error || error.message}`,
        severity: 'error'
      });
    } finally {
      setIsLoading(false);
    }
  };
  
  //   
  const handleExportFile = async () => {
    if (!data.length) {
      setSnackbar({
        open: true,
        message: '  ',
        severity: 'warning'
      });
      return;
    }
    
    setIsLoading(true);
    
    try {
      //       
      const safeFilename = (filename || 'export')
        .replace(/\s+/g, '_')
        .replace(/[^a-zA-Z0-9_.-]/g, '')
        .toLowerCase();
      
      //    .xlsx 
      const fileWithExt = safeFilename.endsWith('.xlsx') ? safeFilename : safeFilename + '.xlsx';
      
      console.log('  :', { 
        filename: fileWithExt,
        originalFilename: filename,
        dataLength: data.length,
        columns: columns //   
      });
      
      const response = await axios.post('/api/export', {
        data,
        filename: fileWithExt,
        columns: columns //    
      });
      
      console.log('  :', response.data);
      
      if (response.data.success) {
        //  
        const token = localStorage.getItem('token');
        const userId = user?.id;
        
        if (!token || !userId) {
          throw new Error('  .  .');
        }
        
        try {
          //  URL  URL    URL 
          let downloadUrl = response.data.download_url;
          // downloadUrl /     
          if (downloadUrl.startsWith('/')) {
            downloadUrl = `${axios.defaults.baseURL}${downloadUrl}`;
          }
          
          console.log('GET    URL:', downloadUrl);
          
          //     
          const headers = {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          };
          
          console.log(' :', headers);
          
          //   
          const downloadResponse = await axios({
            method: 'GET',
            url: downloadUrl,
            responseType: 'blob',
            headers: headers
          });
          
          console.log(' :', downloadResponse.status, downloadResponse.headers);
          
          //   
          if (!downloadResponse.data) {
            throw new Error('   ');
          }
          
          // Blob URL   
          const blob = new Blob([downloadResponse.data]);
          const url = window.URL.createObjectURL(blob);
          const link = document.createElement('a');
          link.href = url;
          link.download = response.data.filename || fileWithExt;
          document.body.appendChild(link);
          link.click();
          window.URL.revokeObjectURL(url); //   
          document.body.removeChild(link);
          
          setSnackbar({
            open: true,
            message: '  ',
            severity: 'success'
          });
        } catch (downloadError) {
          console.error('   :', downloadError);
          console.error(' :', downloadError.response || downloadError.message);
          
          //    
          let errorMessage = '    ';
          if (downloadError.response) {
            if (downloadError.response.status === 404) {
              errorMessage += ':     (404).   .';
            } else if (downloadError.response.status === 401) {
              errorMessage += ':   (401).  .';
            } else {
              errorMessage += `:   (${downloadError.response.status})`;
            }
          } else {
            errorMessage += `: ${downloadError.message}`;
          }
          
          setSnackbar({
            open: true,
            message: errorMessage,
            severity: 'error'
          });
        }
      } else {
        throw new Error(response.data.error || '  ');
      }
    } catch (error) {
      console.error('    :', error);
      setSnackbar({
        open: true,
        message: `    : ${error.response?.data?.error || error.message}`,
        severity: 'error'
      });
    } finally {
      setIsLoading(false);
    }
  };
  
  //   
  const handleApplyTemplate = (templateOperations) => {
    setOperations(templateOperations);
    setSnackbar({
      open: true,
      message: ` . ${templateOperations.length}  .`,
      severity: 'success'
    });
  };
  
  //    
  const handleAddToOperation = (type, valueData) => {
    //        
    if (type === 'replace') {
      //    
      const newOperation = {
        type: 'replace',
        sourceColumn: valueData.sourceColumn,
        targetColumn: valueData.sourceColumn, //    
        searchValue: valueData.searchValue,
        replaceValue: '' //  
      };
      
      setOperations([...operations, newOperation]);
      setSnackbar({
        open: true,
        message: `"${valueData.sourceColumn}"  "${valueData.searchValue}"     `,
        severity: 'success'
      });
    } 
    else if (type === 'conditional_replace') {
      //    
      const newOperation = {
        type: 'conditional_replace',
        conditions: [{
          column: valueData.column,
          value: valueData.value
        }],
        targetColumn: '', //   
        resultValue: '' //   
      };
      
      setOperations([...operations, newOperation]);
      setSnackbar({
        open: true,
        message: `"${valueData.column}"  "${valueData.value}"      `,
        severity: 'success'
      });
    }
    else if (type === 'conditional_aggregate') {
      //    
      const newOperation = {
        type: 'conditional_aggregate',
        conditionColumn: valueData.column,
        conditionValue: valueData.value,
        aggregateColumn: '', //   
        resultColumn: '', //   
        saveToDashboard: false
      };
      
      setOperations([...operations, newOperation]);
      setSnackbar({
        open: true,
        message: `"${valueData.column}"  "${valueData.value}"      `,
        severity: 'success'
      });
    }
  };
  
  // Snackbar  
  const handleCloseSnackbar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setSnackbar({ ...snackbar, open: false });
  };
  
  //   
  const handleNavigateToDashboard = () => {
    setCurrentTab(1);
  };

  //   UI 
  return (
    <ThemeProvider theme={themes[currentTheme]}>
      <CssBaseline />
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
          <Toolbar>
            <StorageIcon sx={{ mr: 2 }} />
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                 
            </Typography>
            
            {user && (
              <>
                <Tabs
                  value={currentTab}
                  onChange={handleTabChange}
                  textColor="inherit"
                  indicatorColor="secondary"
                  sx={{ mr: 2 }}
                >
                  <Tab label=" " />
                  <Tab label="" />
                </Tabs>
                
                <IconButton color="inherit" onClick={handleThemeMenuOpen}>
                  <PaletteIcon />
                </IconButton>
                
                <IconButton 
                  color="inherit" 
                  edge="end" 
                  onClick={handleUserMenuOpen}
                  sx={{ ml: 1 }}
                >
                  <AccountCircleIcon />
                </IconButton>
                
                <Menu
                  anchorEl={userMenuAnchorEl}
                  open={Boolean(userMenuAnchorEl)}
                  onClose={handleUserMenuClose}
                >
                  <MenuItem disabled>
                    <Typography variant="body2" color="textSecondary">
                      {user ? user.username : ''}
                    </Typography>
                  </MenuItem>
                  {user && user.is_admin && (
                    <MenuItem onClick={handleAdminDashboard}>
                      <AdminPanelSettingsIcon fontSize="small" sx={{ mr: 1 }} />
                       
                    </MenuItem>
                  )}
                  <MenuItem onClick={handleLogout}>
                    <LogoutIcon fontSize="small" sx={{ mr: 1 }} />
                    
                  </MenuItem>
                </Menu>
              </>
            )}
            
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleThemeMenuClose}
            >
              {Object.keys(themes).map((theme) => (
                <MenuItem
                  key={theme}
                  onClick={() => handleThemeChange(theme)}
                  selected={currentTheme === theme}
                >
                  {themes[theme].palette.name}
                </MenuItem>
              ))}
            </Menu>
          </Toolbar>
        </AppBar>
        
        <Container maxWidth="xl" sx={{ mt: 3 }}>
          {user && currentTab === 0 && (
            <Box>
              <Box sx={{ display: 'flex', mt: 3, mb: 2 }}>
                <Box sx={{ width: '60%', pr: 2 }}>
                  <FileUpload onFileUpload={handleFileUpload} />
                  
                  {data.length > 0 && (
                    <Box sx={{ mt: 2 }}>
                      <DataTable data={data} columns={columns} />
                    </Box>
                  )}
                </Box>
                
                <Box sx={{ width: '40%' }}>
                  <TemplateManager
                    operations={operations}
                    onApplyTemplate={handleApplyTemplate}
                  />
                  
                  <Box sx={{ mt: 2 }}>
                    <OperationsPanel
                      columns={columns}
                      operations={operations}
                      setOperations={setOperations}
                      onProcessData={handleProcessData}
                      onExportFile={handleExportFile}
                      onNavigateToDashboard={handleNavigateToDashboard}
                    />
                  </Box>
                  
                  {columns.length > 0 && (
                    <Box sx={{ mt: 2 }}>
                      <UniqueValuesList 
                        data={data} 
                        columns={columns}
                        onAddToOperation={handleAddToOperation}
                      />
                    </Box>
                  )}
                </Box>
              </Box>
              
              {logs.length > 0 && (
                <Box sx={{ mt: 2, mb: 4 }}>
                  <OperationLogs logs={logs} totalAffectedRows={totalAffectedRows} />
                </Box>
              )}
            </Box>
          )}
          
          {user && currentTab === 1 && (
            <Box sx={{ mt: 2 }}>
              <Dashboard />
            </Box>
          )}
        </Container>
      </Box>
      
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbar.severity} sx={{ width: '100%' }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
      
      <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={isLoading}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
    </ThemeProvider>
  );
}

function App() {
  return (
    <ThemeProvider theme={themes['default']}>
      <CssBaseline />
      <Router>
        <Routes>
          <Route path="/login" element={<Login onLogin={(user) => {}} />} />
          <Route path="/register" element={<Register />} />
          <Route 
            path="/*" 
            element={
              <ProtectedRoute>
                <AppContent />
              </ProtectedRoute>
            } 
          />
          <Route 
            path="/admin/*" 
            element={
              <ProtectedRoute requireAdmin={true}>
                <AdminDashboard />
              </ProtectedRoute>
            } 
          />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App; 