import React, { useState, useCallback } from 'react';
import { Box, Paper, Typography, Button, CircularProgress, Alert } from '@mui/material';
import { styled } from '@mui/material/styles';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import { useDropzone } from 'react-dropzone';
import axios from 'axios';

//  
const DropzoneBox = styled(Paper, {
  shouldForwardProp: (prop) => prop !== 'isDragActive'
})(({ theme, isDragActive }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  padding: theme.spacing(4),
  borderWidth: 2,
  borderRadius: theme.shape.borderRadius,
  borderColor: isDragActive ? theme.palette.primary.main : theme.palette.divider,
  borderStyle: 'dashed',
  backgroundColor: isDragActive ? theme.palette.action.hover : theme.palette.background.paper,
  color: theme.palette.text.primary,
  outline: 'none',
  transition: 'border .24s ease-in-out',
  cursor: 'pointer',
  '&:hover': {
    borderColor: theme.palette.primary.main,
  },
}));

function FileUpload({ onFileUpload }) {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const onDrop = useCallback(async (acceptedFiles) => {
    //   
    if (acceptedFiles.length === 0) {
      setError('  ');
      return;
    }

    const file = acceptedFiles[0];
    const fileExtension = file.name.split('.').pop().toLowerCase();

    if (!['xlsx', 'xls', 'csv'].includes(fileExtension)) {
      setError('  CSV   ');
      return;
    }

    try {
      setIsLoading(true);
      setError('');
      setSuccess('');

      const formData = new FormData();
      formData.append('file', file);

      const response = await axios.post('/api/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      console.log(' :', response);
      console.log('  :', JSON.stringify(response.data, null, 2));
      
      //   
      const responseData = response.data;
      console.log('  :', typeof responseData);
      console.log('  :', Object.keys(responseData));
      
      console.log('  :', responseData.data && Array.isArray(responseData.data) ? responseData.data.length : 0);
      console.log(' :', responseData.columns);
      
      //  
      if (responseData.data && responseData.data.length > 0) {
        console.log('   :', responseData.data[0]);
        console.log('   :', Object.keys(responseData.data[0]));
      }

      if (responseData.success) {
        setSuccess(`"${file.name}"   `);
        console.log('onFileUpload  :', responseData);
        
        // onFileUpload      
        if (typeof onFileUpload === 'function') {
          onFileUpload(responseData);
        } else {
          console.error('onFileUpload  :', typeof onFileUpload);
        }
      } else {
        setError(responseData.error || '   ');
      }
    } catch (error) {
      console.error('   :', error);
      setError(error.response?.data?.error || '    ');
    } finally {
      setIsLoading(false);
    }
  }, [onFileUpload]);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': ['.xlsx'],
      'application/vnd.ms-excel': ['.xls'],
      'text/csv': ['.csv'],
    },
    multiple: false,
  });

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ mb: 2 }}>
        <Typography variant="h6" gutterBottom>
            
        </Typography>
        <Typography variant="body2" color="text.secondary" gutterBottom>
          (.xlsx, .xls)  CSV      .
        </Typography>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }}>
          {success}
        </Alert>
      )}

      <DropzoneBox {...getRootProps()} isDragActive={isDragActive}>
        <input {...getInputProps()} />
        <CloudUploadIcon color="primary" sx={{ fontSize: 48, mb: 2 }} />
        {isLoading ? (
          <CircularProgress size={24} />
        ) : (
          <>
            <Typography variant="body1" align="center">
              {isDragActive
                ? '  '
                : '    '}
            </Typography>
            <Box sx={{ mt: 2 }}>
              <Button variant="contained" color="primary">
                 
              </Button>
            </Box>
          </>
        )}
      </DropzoneBox>
    </Box>
  );
}

export default FileUpload; 