import React, { useState, useEffect } from 'react';
import {
  Box,
  Paper,
  Typography,
  TextField,
  Button,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Divider,
  Alert,
  CircularProgress
} from '@mui/material';
import SaveIcon from '@mui/icons-material/Save';
import DeleteIcon from '@mui/icons-material/Delete';
import FolderOpenIcon from '@mui/icons-material/FolderOpen';
import axios from 'axios';

function TemplateManager({ operations, onApplyTemplate }) {
  const [templateName, setTemplateName] = useState('');
  const [templates, setTemplates] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [openDialog, setOpenDialog] = useState(false);
  const [selectedTemplate, setSelectedTemplate] = useState(null);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [templateToDelete, setTemplateToDelete] = useState(null);

  //   
  const fetchTemplates = async () => {
    try {
      setLoading(true);
      setError('');
      
      const response = await axios.get('/api/templates');
      setTemplates(response.data.templates || []);
    } catch (error) {
      console.error('  :', error);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  //      
  useEffect(() => {
    fetchTemplates();
  }, []);

  //   
  const handleSaveTemplate = async () => {
    if (!templateName.trim()) {
      setError('  .');
      return;
    }

    if (!operations || operations.length === 0) {
      setError('  .   .');
      return;
    }

    try {
      setLoading(true);
      setError('');
      setSuccess('');

      const response = await axios.post('/api/templates', {
        name: templateName,
        operations: operations
      });

      if (response.data.success) {
        setSuccess('  .');
        setTemplateName('');
        fetchTemplates();
      }
    } catch (error) {
      console.error('  :', error);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  //    
  const handleViewTemplate = (template) => {
    setSelectedTemplate(template);
    setOpenDialog(true);
  };

  //   
  const handleApplyTemplate = () => {
    if (selectedTemplate && selectedTemplate.operations) {
      onApplyTemplate(selectedTemplate.operations);
      setOpenDialog(false);
      setSuccess(`'${selectedTemplate.name}'  .`);
    }
  };

  //     
  const handleCloseDialog = () => {
    setOpenDialog(false);
  };

  //     
  const handleOpenDeleteDialog = (template, event) => {
    event.stopPropagation();
    setTemplateToDelete(template);
    setDeleteDialogOpen(true);
  };

  //     
  const handleCloseDeleteDialog = () => {
    setDeleteDialogOpen(false);
    setTemplateToDelete(null);
  };

  //   
  const handleDeleteTemplate = async () => {
    if (!templateToDelete) return;
    
    try {
      setLoading(true);
      setError('');
      setSuccess('');
      
      // :  ID 
      console.log("  ID:", templateToDelete.id);
      
      if (!templateToDelete.id) {
        setError(' ID  .');
        setLoading(false);
        return;
      }
      
      const response = await axios.delete(`/api/templates/${templateToDelete.id}`);
      
      if (response.data.success) {
        setSuccess(`'${templateToDelete.name}'  .`);
        fetchTemplates();
      }
    } catch (error) {
      console.error('  :', error);
      setError('    .');
    } finally {
      setLoading(false);
      setDeleteDialogOpen(false);
      setTemplateToDelete(null);
    }
  };

  //    
  const getOperationTypeText = (type) => {
    switch (type) {
      case 'replace':
        return ' ';
      case 'conditional_replace':
        return ' ';
      case 'conditional_aggregate':
        return ' ';
      default:
        return '  ';
    }
  };

  //   
  const getOperationDescription = (operation) => {
    if (operation.type === 'replace') {
      return `${operation.sourceColumn}  '${operation.searchValue}' ${operation.targetColumn}  '${operation.replaceValue}' `;
    } else if (operation.type === 'conditional_replace') {
      const conditions = operation.conditions?.map(c => `${c.column}='${c.value}'`).join('  ') || '';
      return ` (${conditions})  ${operation.targetColumn}  '${operation.resultValue}' `;
    } else if (operation.type === 'conditional_aggregate') {
      return `${operation.conditionColumn}  '${operation.conditionValue}'  ${operation.aggregateColumn}   `;
    }
    return '';
  };

  return (
    <Box>
      <Typography variant="h6" gutterBottom>
         
      </Typography>

      <Paper sx={{ p: 2, mb: 3 }}>
        <Typography variant="subtitle2" gutterBottom>
            
        </Typography>
        <Box sx={{ mb: 2 }}>
          <TextField
            fullWidth
            size="small"
            label=" "
            value={templateName}
            onChange={(e) => setTemplateName(e.target.value)}
            sx={{ mb: 2 }}
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleSaveTemplate}
            disabled={loading || !templateName.trim() || operations.length === 0}
            startIcon={<SaveIcon />}
          >
            {loading ? <CircularProgress size={24} /> : ' '}
          </Button>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        {success && (
          <Alert severity="success" sx={{ mb: 2 }}>
            {success}
          </Alert>
        )}
      </Paper>

      <Paper sx={{ p: 2 }}>
        <Typography variant="subtitle2" gutterBottom>
           
        </Typography>

        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', p: 2 }}>
            <CircularProgress size={24} />
          </Box>
        ) : templates.length === 0 ? (
          <Alert severity="info">
              .     .
          </Alert>
        ) : (
          <List>
            {templates.map((template, index) => (
              <React.Fragment key={template.id}>
                {index > 0 && <Divider />}
                <ListItem>
                  <ListItemText
                    primary={template.name}
                    secondary={` ${template.operations?.length || 0}`}
                  />
                  <ListItemSecondaryAction>
                    <IconButton
                      edge="end"
                      onClick={() => handleViewTemplate(template)}
                      color="primary"
                      sx={{ mr: 1 }}
                    >
                      <FolderOpenIcon />
                    </IconButton>
                    <IconButton
                      edge="end"
                      onClick={(event) => handleOpenDeleteDialog(template, event)}
                      color="error"
                    >
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              </React.Fragment>
            ))}
          </List>
        )}
      </Paper>

      {/*    */}
      <Dialog
        open={openDialog}
        onClose={handleCloseDialog}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          : {selectedTemplate?.name}
        </DialogTitle>
        <DialogContent dividers>
          {selectedTemplate?.operations?.length > 0 ? (
            <List>
              {selectedTemplate.operations.map((operation, index) => (
                <ListItem key={index}>
                  <ListItemText
                    primary={getOperationTypeText(operation.type)}
                    secondary={getOperationDescription(operation)}
                  />
                </ListItem>
              ))}
            </List>
          ) : (
            <Typography color="text.secondary">
                 .
            </Typography>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog} color="inherit">
            
          </Button>
          <Button
            onClick={handleApplyTemplate}
            color="primary"
            variant="contained"
            disabled={!selectedTemplate?.operations?.length}
          >
              
          </Button>
        </DialogActions>
      </Dialog>

      {/*     */}
      <Dialog
        open={deleteDialogOpen}
        onClose={handleCloseDeleteDialog}
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <Typography>
            '{templateToDelete?.name}'  ?
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                .
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDeleteDialog} color="inherit">
            
          </Button>
          <Button onClick={handleDeleteTemplate} color="error" variant="contained">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default TemplateManager; 